.row__posters {

    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
    }

.row__posters::-webkit-scrollbar{
    display: none;
}
.row__poster {

object-fit:contain;
width:100%;
max-height: 150px;
margin-right: 5px;
transition: transform 450ms;
}
.row__poster:hover {

 transform:  scale(1.1);

}
.row_posterLarge{
    
    max-height: 250px;
    }
.row_posterLarge:hover{
    transform:  scale(1.1);

        }

.row{

    color: blanchedalmond;
    margin-left: 8px;
}